.sectionWrap {
    overflow: hidden; 
    position: relative;
    height: 100vh;
    background: #1a2229;
}

.topContainer {
   display: flex;
   flex-direction: row;
 
 }
 
 .copyContainer {
   display: flex;
   flex-direction: column;
   align-items: center;
   width: 100%;
 }
 
 .header {
   line-height: 48px;
   font-weight: 200;
   font-size: 48px;
   margin-bottom: 16px;
   text-transform: uppercase;
   color: #76d5d8;
 }
 
 .paragraph {
   color: #fff;
   font-size: 14px;
   line-height: 24px;
   margin: 0;
   text-align: center;
   width: 50%;
 }

.btnContainer {
   display: flex;
   position: relative;
   gap: 2rem;
   justify-content: center;
   flex-direction: row;
   margin: auto;
   width: 100%; 
   top: 60%;
}

.btnWrap {
    display: grid;
    place-content: center; 
    border-radius: 9999px;
    background-color: transparent;
    border: #76D5D8 solid 1px;
    color: #fff;
    width: 3.5rem; 
    height: 3.5rem; 
    font-size: 1.875rem;
    line-height: 2.25rem; 
    transition-property: color, background-color, border-color, text-decoration-color, fill, stroke;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 300ms; 
}

.btnWrap:hover {
    color: #000; 
    background-color: #76D5D8; 
 }

 .cardContainer {
    position: absolute; 
    top: 50%; 
    left: 50%; 
    padding: 8px; 
    border-radius: 16px;
    border: 1px solid transparent;
    color: #000000; 
    transition-property: color, fill;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 300ms; 
    transition-duration: 500ms; 
    cursor: pointer; 
 }

 .cardActive {
    z-index: 10; 
    background-color: #1A2229; 
    box-shadow: #76D5D8 0px 0px 30px 0px;
    border: 1px solid #76D5D8;
 }

 .cardInactive {
    z-index: 0; 
    border: 1px solid #76D5D8;
    background-color: #1A2229;
    filter: blur(3px);
   
    /* box-shadow: #00000050 0px 0px 10px 0px; */
 }

 .cardWrap {
    display: block; 
    object-fit: cover; 
    position: absolute; 
    background-color: #000000; 
    --transform-rotate: 45deg; 
    transform-origin: top right; 
 }

 .cardImage {
    object-fit: cover; 
    object-position: center; 
    margin-bottom: 1rem; 
    border-radius: 8px;
    width: 100%; 
    height: 50%; 

 }

 .cardHeader {
    font-size: 32px;
    font-weight: 200;
    text-transform: uppercase;
    line-height: 1.5rem; 
    margin-left: 16px;

@media (min-width: 640px) { 
    font-size: 32px;
    line-height: 1.75rem; 
 }
 }

 .cardHeaderActive {
    color: #ffffff;
 }

 .cardHeaderInactive {
    color: #ffffff;
 }

 .cardDesc {
    position: absolute; 
    margin-left: 16px;
    margin-right: 32px;
    margin-top: 8px; 
    font-size: 0.875rem;
    line-height: 1.25rem; 
 }

 .cardDescActive {
    color: #ffffff;
 }

 .cardDescInactive {
    color: #ffffff;
 }